@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0; }

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

:focus,
:active {
  outline: none; }

a:focus,
a:active {
  outline: none; }

nav,
footer,
header,
aside {
  display: block; }

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input,
button,
textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400; }

body {
  font-family: "Open Sans"; }

/*---------------*/


.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 50; }
  .header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #50b848;
    z-index: 3;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); }
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1152px;
    margin: 0 auto;
    padding: 10px; }
  .header__logo {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 4; }
    .header__logo img {
      max-width: 100%;
      margin-right: 14px;
      z-index: 4; }
  .header__title {
    font-size: 22px;
    font-weight: 700;
    color: #f0d43a;
    margin-bottom: 5px; }
  .header__subtitle {
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
    color: #f0d43a; }
  .header__menu {
    display: flex;
    align-items: center; }
  .header__list {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 3; }
    .header__list li {
      margin-right: 20px; }
  .header__link {
    font-size: 18px;
    font-weight: 400;
    color: #f0d43a; }
    .header__link:hover {
      color: #287b1a; }
  .header__button {
    position: relative;
    font-size: 18px;
    font-weight: 700;
    color: #287b1a;
    background-color: #f0d43a;
    border-radius: 2px;
    padding: 8px 30px;
    z-index: 3;
    transition: all 0.1ms ease 0.1s; }
    .header__button:hover {
      color: #f0d43a;
      background-color: #287b1a; }

